.header {
    background: #FFFFFF;
    border: 1.5px solid var(--main-color);
    border-radius: 0px 0px 8px 8px;
    min-width: 320px;
    margin-left: auto;
    margin-right: auto;
}

a {
    height: 28px;
}

.container {
    height: 68px;
    min-width: 320px;
    padding: 0 32px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-nav-list li {
    display: inline-block;
}

.header-home::after {
    background-color: var(--background-active-color);
    outline: none;
    border-radius: 36px;
}

.header-burger {
    width: 28px;
    height: 28px;
    border-radius: 0;
    border-color: transparent;
    background-color: transparent;
}
.header-burger:hover {
    border-color: transparent;
    background-color: transparent;
}
// .theme-switch input[type="checkbox"] {
//     // display: none;
// }

.theme-switch {
    display: flex;
    justify-content: center;
    align-items: center;
}

.switch {
    margin-right: 14px;
    padding-top: 15px;
    cursor: pointer;
}


.switch-theme-check:checked+.slider-round {
background-image: url("../images/Switcher-on.png");
}
.slider-round{
display: inline-block;
    width: 40px;
    height: 20px;
    background-image: url("../images/Switcher-off.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
.header-home {
    height: 34px;
    background-color: transparent;
    border: transparent;
    padding: 8px 16px;
}



