.header-burger-icon {
    width: 28px;
    height: 28px;
    stroke: var(--main-color);
}
.header-burger-icon:hover {
    stroke: var(--background-main-color);
}

@media screen and (max-width: 768px) {
    .header-nav {
        display: none;
    }

}

@media screen and (min-width: 768px) {


.header-nav {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
    }

    .nav {
        height: 72px;
        justify-content: normal;
        align-items: center;
    }

    .header-nav-icon {
        width: 20px;
        height: 20px;
        position: absolute;
        color: var(--main-color);
    }

    .header-nav-link {
        font-weight: 500;
        line-height: 1.28;
        text-transform: uppercase;
        color: var(--main-color);
    }

    .header-burger {
        display: none;
    }

    .header-logo {
        margin-right: 40px;
    }

}

@media screen and (min-width: 1200px) {
    .slider-round {
    display: inline-block;
    width: 40px;
    height: 24px;
    background-image: url("../images/switcher-off@2x.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

.header-logo {
    margin-right: 76px;
}

}

